import React from 'react';
import './Body.css';
export default function Body(){
    console.log("click here does nothing sorry")
    return(
        <>
            <div className="bod">
                <div className="navbar">
                <div className="nnav">
                    <h1 className="log">LOGO</h1>
                    <div className="info">
                        <h1 className="home">Home</h1>
                        <h1 className="about">About</h1>
                    </div>
                </div>
                </div>
                <div className="main-body">
                    <div className="center">
                        <h1 className="click">Do not click here</h1>
                    </div>
                </div>
            </div>
        </>
    )
}
window.addEventListener("load",()=>{
    
    var log=document.querySelector(".log")
    var info=document.querySelector(".info")
    var click=document.querySelector(".click")
    log.style.opacity=1
    log.style.transform="translateY(10px)"
    log.style.transition="1s ease-in-out"
    info.style.transform="translateY(10px)"
    info.style.transition="1s ease-in-out"
    click.style.transform="translateY(10px)"
    click.style.transition="1s ease-in-out"
    click.style.transform="translateY(-10px)"
    click.style.transition="1s ease-in-out"
    
})

