import React from 'react';
import './Third.css';
export default function Third(){
    return(
        <>
            <div className="lowmid">
                <div className="textndimages">
                    <div className="Image1"></div>
                    <div className="Text">
                        <h1 className="hd">React basic info</h1>
                        <ul className="hlist">
                            <li className="sublist">ReactJS is an open-source front-end JavaScript library to build the user interface. It is maintained by Facebook and used by many companies today for UI development.</li>
                            <li className="sublist">The core features of ReactJS includes JSX, components(functional components and class-based components), the life cycle of a component, props, and state support for a component, working with javascript expressions.</li>
                            <li className="sublist">ReactJS allows you to work with external css as well as inline css using javascript expression.</li>
                        </ul>
                    </div>
                </div>
            </div>
        </>
    )
}

window.addEventListener("scroll",()=>{
    var a=window.scrollY
    var image=document.querySelector(".Image1")
    var text=document.querySelector(".Text")
    if(a>1000)
    {   
        text.style.opacity=1
        text.style.transform="translateX(-10px)"
        text.style.transition="0.5s ease-in-out"
        image.style.opacity=1
        image.style.transform="translateX(10px)"
        image.style.transition="0.5s ease-in-out"
    }
    else{
        text.style.opacity=0
        text.style.transform="translateX(10px)"
        text.style.transition="0.5s ease-in-out"
        image.style.opacity=0
        image.style.transform="translateX(-10px)"
        image.style.transition="0.5s ease-in-out"
    }
   
    
    
})

