import React from 'react';
import './Sec.css';
export default function Sec(){
    return(
        <>
            <div className="middile">
                <div className="text-image">
                    <div className="text">
                        <h1 className="heading">Reasons Why i love React</h1>
                        <ul className="mainlist">
                            <li className="sub">The Reusability Factor</li>
                            <li className="sub"> Performance Enhancement</li>
                            <li className="sub">Known to be SEO Friendly</li>
                        </ul>
                    </div>
                    <div className='image'></div>
                </div>
            </div>
        </>
    )
}
window.addEventListener("scroll",()=>{
    var t=window.scrollY
    var image=document.querySelector(".image")
    var text=document.querySelector(".text")
    if(t>390)
    {   
        text.style.opacity=1
        text.style.transform="translateX(10px)"
        text.style.transition="0.5s ease-in-out"
        image.style.opacity=1
        image.style.transform="translateX(-10px)"
        image.style.transition="0.5s ease-in-out"
    }
    else{
        text.style.opacity=0
        text.style.transform="translateX(-10px)"
        text.style.transition="0.5s ease-in-out"
        image.style.opacity=0
        image.style.transform="translateX(10px)"
        image.style.transition="0.5s ease-in-out"
    }
   
    
    
})
